
import React from 'react';
import { withTranslation } from "react-i18next"

import {
  Grid,
  Typography,
} from "@material-ui/core";

// components
import LoginUI from '../../components/Firebase';

// Styles
import useStyles from "./styles";

// logo
import logo from "./logo.svg";

const I18N_ID = 'Others';

function FBLogin({
  t,
}) {
    const classes = useStyles();

    return (
        <Grid container className={classes.container}>
        <div className={classes.logotypeContainer}>
          <img src={logo} alt="logo" className={classes.logotypeImage} />
          <Typography className={classes.logotypeText}>CARBON</Typography>
        </div>
        <div className={classes.formContainer}>
          <div className={classes.form}>
            <Typography variant="h2" className={classes.greeting}>
              Hey, User
            </Typography>

            <div className={classes.formDividerContainer}>
                <div className={classes.formDivider} />
            </div>

            <LoginUI />

            <div className={classes.formDividerContainer}>
                <div className={classes.formDivider} />
            </div>

            <Typography color="primary" className={classes.copyright}>
                Â© 2014-2019 CARBON, All rights reserved.
            </Typography>
          </div>
        </div>
      </Grid>        
    );
}

export default withTranslation(I18N_ID)(FBLogin);
